import * as React from 'react';
import './Bodegas.css';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import MediaCard from './Card'
import {useEffect, useState } from 'react'
import equusCarga from '../../img/equuscarga.png';
import db from '../../Firebase/Firebase';
import {collection, getDocs } from 'firebase/firestore'
import wines2 from '../../img/bodega.jpg'



export default function Bodegas() {

  
  const [products, setProducts] = useState([])
  const [loading, setLoading] = useState(true)

  const getProducts = async() =>{
    const itemCollection = collection(db, 'Bodegas');
    const bodegasSnapshot = await getDocs(itemCollection);
    const bodegasList = bodegasSnapshot.docs.map( (doc)=>{
      let bodega = doc.data();
      bodega.id = doc.id;
      return bodega
      }
    )
    console.log(bodegasList)
    return bodegasList;
  }

  useEffect(()=>{
    setProducts([]);
    getProducts().then((bodegas)=>{
      setProducts(bodegas)
    });
    setLoading(false)
  },[])

  return (
    <div>
      {loading ? (
          <img src={equusCarga} alt={equusCarga} />
        ) : (
          <div>
            <div className='img-background-container'>
              <img className='img-background2' src={wines2}/>
            </div>
            <div className='title-container'>
                <h2 className='title'>Bodegas</h2>
            </div>
            <div>
              <Box sx={{ flexGrow: 1, width: '50%', margin: 'auto' }}>
                  <Grid container rowSpacing={8} columnSpacing={{ xs: 1, sm: 2, md: 4 }} spacing={12} className='grids'>
                      {products.map((product)=>{
                        return(
                        <Grid  item xs={12} sm={6} md={4}>
                          <MediaCard key={product.id} data={product}/>
                        </Grid>
                        )
                        })}
                  </Grid>
              </Box>
            </div>
          </div>
        )}
    </div>
  );
}